@font-face {
  font-family: Bebas-Neue;
  src: url("BebasNeue-Regular.53e308f6.ttf");
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Regular.52ca3563.ttf");
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Bold.6a1a9a4d.ttf");
  font-weight: bold;
}

html, body, header, h1, h2, h3, p, a, div, nav, button, section, label, ul, li {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

@keyframes move-up-then-down {
  0% {
    top: 0;
  }

  50% {
    top: -27px;
  }

  100% {
    top: -14px;
  }
}

@keyframes shake-while-oving-up {
  0% {
    transform: rotateZ(0);
  }

  25% {
    transform: rotateZ(-10deg);
  }

  50% {
    transform: rotateZ(0);
  }

  75% {
    transform: rotateZ(10deg);
  }

  100% {
    transform: rotateZ(0);
  }
}

@keyframes shake-while-moving-down {
  0% {
    transform: rotateZ(0);
  }

  80% {
    transform: rotateZ(3deg);
  }

  90% {
    transform: rotateZ(-3deg);
  }

  100% {
    transform: rotateZ(0);
  }
}

@keyframes slide-bottom {
  0% {
    transform: translateY(0)rotate(90deg);
  }

  100% {
    transform: translateY(40px)rotate(90deg);
  }
}

@keyframes rotating {
  from {
    transform: rotate(360deg);
  }

  to {
    transform: rotate(0);
  }
}

@keyframes stroke {
  0% {
    fill: rgba(3, 132, 158, .247);
    stroke: #03839e;
    stroke-dashoffset: 25%;
    stroke-dasharray: 0 50%;
    stroke-width: 1px;
  }

  70% {
    stroke: #03839e;
  }

  80% {
    stroke: #03839e;
    stroke-width: 2px;
  }

  100% {
    fill: rgba(0, 0, 0, 0);
    stroke: #03839e;
    stroke-dashoffset: -25%;
    stroke-dasharray: 50% 0;
    stroke-width: 1px;
  }
}

@keyframes scale-up-center {
  0% {
    transform: scale(.1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes swirl-out-bck {
  0% {
    opacity: 1;
    transform: rotate(0)scale(1);
  }

  100% {
    opacity: 0;
    transform: rotate(-540deg)scale(0);
  }
}

.header {
  padding: 15px 30px 110px;
  position: relative;
  overflow: hidden;
}

@media (min-width: 640px) {
  .header {
    --columns-numbr: 6;
    --row-numbr: 4;
    --columns-gap: 20px;
    grid-template-columns: repeat(var(--columns-numbr), 1fr);
    grid-template-rows: repeat(var(--row-numbr), auto);
    column-gap: var(--columns-gap);
    min-width: 640px;
    padding: 30px 34px 109px;
    display: grid;
  }
}

@media (min-width: 1280px) {
  .header {
    --columns-numbr: 12;
    --row-numbr: 4;
    --columns-gap: 30px;
    padding: 20px 135px 150px;
  }
}

