.nav-bar__item {
  color: #4f4f4f;
  text-align: center;
  margin-right: 50px;
  padding: 1px 5px;
  font-family: Manrope, Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 24px;
  display: block;
  position: relative;
}

.nav-bar__item:last-child {
  margin-right: 0;
}

.nav-bar__item:before, .nav-bar__item:after {
  content: "";
  opacity: 0;
  background-color: #03839e;
  width: 100%;
  height: 1.5px;
  transition: all .3s;
  position: absolute;
  left: 0;
}

.nav-bar__item:before {
  top: 0;
  transform: translateY(10px);
}

.nav-bar__item:after {
  content: "";
  height: 2px;
  bottom: 0;
  transform: translateY(-10px);
}

.nav-bar__item:hover:before, .nav-bar__item:hover:after {
  opacity: 1;
  transform: translateY(0);
}

