.popup-menu {
  pointer-events: none;
  z-index: 1;
  background-color: #03839e;
  width: 231px;
  height: 100%;
  padding: 138px 0 0 51px;
  transition: all .5s;
  position: fixed;
  top: 0;
  right: 0;
  overflow: hidden;
  transform: translateX(100%);
}

@media (min-width: 1280px) {
  .popup-menu {
    display: none;
  }
}

.popup-menu:target {
  pointer-events: all;
  transform: translateX(0);
}

.popup-menu__item {
  color: #f8f8f8;
  z-index: 1;
  border: 1px solid #03839e;
  border-radius: 5px;
  margin-bottom: 15px;
  margin-right: 20px;
