}

.popup-menu__item_counter:before {
  content: "0";
  background-color: #eb5757;
  border-radius: 50px;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  width: 14px;
  height: 14px;
  font-family: Bebas-Neue, Arial, Helvetica, sans-serif;
  font-size: 9.17px;
