  height: 27px;
  display: block;
  position: relative;
}

.burger__link:before, .burger__link:after {
  content: "";
  background-color: #333;
  width: 100%;
  height: 4px;
  transition: all .7s cubic-bezier(.68, -.55, .27, 1.55);
  position: absolute;
  left: 0;
}

.burger__link:before {
  top: 6px;
}

.burger__link:after {
  top: 14px;
}

.burger__text {
  background-color: #333;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 22px;
  left: 0;
}

.burger__text:before {
  content: "MENU";
  color: #4f4f4f;
  letter-spacing: 3px;
  font-family: Bebas-Neue, Arial, Helvetica, sans-serif;
  font-size: 12px;
  position: absolute;
  top: 4px;
  left: 0;
  right: 0;
}

.burger:hover .burger__link:before {
  left: -39px;
}

.burger:hover .burger__link:after {
  left: 39px;
}

.burger:hover .burger__text:before {
  animation: .8s .2s forwards move-up-then-down, .8s .2s forwards shake-while-oving-up, .2s .8s forwards shake-while-moving-down;
}

